import { Avatar, Card, Carousel, Dropdown, Icon, Menu, Modal, Rate, Row, Tag } from 'antd';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Marker, StaticGoogleMap } from 'react-static-google-map';
import { querySpecificPublicDocument } from '../../store/actions/queryActions';
import { addToSavedItineraries, duplicateItinerary } from '../../store/actions/saveActions';
import { loadItineraryToState } from '../../store/actions/viewActions';
import { defaultAvatarImage } from '../Core/Globals';
import { cardContainer, carousel, img, item, noReviews, ratingContainer, tagStyle } from './QueryCard.module.scss';
const { Meta } = Card;

const confirm = Modal.confirm;

class QueryCard extends Component {

    constructor(props) {
        super(props);
        this.state = {
            isSaved: false
        }
    }

    duplicateItinerary = () => {
        let that = this;
        confirm({
            title: "Duplicate this itinerary & save it to your itineraries?",
            okText: 'Yes',
            cancelText: 'No',
            onOk() {
                that.props.duplicateItinerary(that.props.itin)
            },
            onCancel() {
                return false;
            },
        });
    }

    saveItinerary = (id, itin) => {
        this.setState({ isSaved: true });

        if (this.state.isSaved) {

        }
        this.props.addToSavedItineraries(id, itin);
    }

    render() {
        const menu = (
            <Menu>
                <Menu.Item>
                    <Icon type="alert" />Report Itinerary
                </Menu.Item>
            </Menu>
        );

        let tags = this.props.itin.tags ? this.props.itin.tags.map((tag) => {
            return <Tag className={tagStyle} color="#1890ff">{tag.toUpperCase()}</Tag>
        }) : [];

        let drop = <Dropdown overlay={menu} placement="bottomLeft"><Icon type="ellipsis" /></Dropdown>

        let locationString = this.props.itin.location.map((location, index) => {
            return <span key={index}>{index + 1 !== this.props.itin.location.length ? location.name + " | " : location.name}</span>
        })

        console.log(this.props.itin)

        let cover = <Carousel dots={false} autoplay className={carousel} effect={"fade"} afterChange={this.onChange}>
            {
                this.props.itin && this.props.itin.meta.images && this.props.itin.meta.images.map((photo) => {
                    return (
                        <img onClick={() => this.props.loadItineraryToState(this.props.itin.id)} className={img} alt="example" src={photo} />
                    )
                })
            }
            {this.props.itin.location.length > 0 &&
                <StaticGoogleMap
                    onClick={() => this.props.loadItineraryToState(this.props.itin.id)}
                    className={img}
                    center={this.props.itin.location[0].name}
                    zoom="13"
                    size="350x200"
                    apiKey="AIzaSyAjMva2YUsi-3w0Nf_hXzDuuH4TDM1qSHA"
                >
                    <Marker location={this.props.itin.location[0].name} />
                </StaticGoogleMap>
            }
        </Carousel>

        return (
            <div className={item}>
                <Card
                    style={{ marginTop: "1em" }}
                    hoverable
                    cover={cover}
                    actions={[<Icon type="heart" style={{ color: this.state.isSaved ? "red" : null }} onClick={() => { this.saveItinerary(this.props.itin.id, this.props.itin) }} />, <Icon type="copy" onClick={() => this.duplicateItinerary()} />, drop]}
                >
                    <div onClick={() => this.props.loadItineraryToState(this.props.itin.id)}>
                        <Row className={ratingContainer}>
                            <Rate allowHalf disabled defaultValue={this.props.itin.rating ? parseFloat((Math.round(this.props.itin.rating * 2) / 2).toFixed(1)) : 0} style={{ fontSize: 15, }} />
                            {
                                (!this.props.itin.rating) && <span className={noReviews}>(No Reviews)</span>
                            }
                            {
                                (this.props.itin.rating && this.props.itin.ratingCount) && <span className={noReviews}>({this.props.itin.ratingCount})</span>
                            }
                        </Row>
                        <Row>
                            <Meta
                                avatar={<Avatar src={!this.props.itin.meta.user.picture ? defaultAvatarImage : this.props.itin.meta.user.picture} />}
                                title={this.props.itin.meta.name}
                                description={locationString}
                            />
                        </Row>
                        <Row className={cardContainer}>
                            {tags.slice(0, 6)}
                        </Row>
                    </div>
                </Card>
            </div>
        )
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        duplicateItinerary: (itinerary) => dispatch(duplicateItinerary(itinerary)),
        addToSavedItineraries: (itinID, itinerary) => dispatch(addToSavedItineraries(itinID, itinerary)),
        loadItineraryToState: (itinID) => dispatch(loadItineraryToState(itinID)),
        querySpecificPublicDocument: (itinID) => dispatch(querySpecificPublicDocument(itinID))
    }
}

export default connect(null, mapDispatchToProps)(QueryCard)