export const addCollectionToCurrentItinerary = () => {
    return (dispatch, getState, { getFirestore }) => {

        dispatch({ type: "START_SAVING" })
        const state = getState();
        const firestore = getFirestore();

        let itinRef = firestore.collection("users").doc(state.firebase.auth.uid).collection("itineraries").doc(state.edit.currentItinID);

        let guid = generateGuid();
        let generatedCollection = generateNewCollection();

        itinRef.set({
            editedAt: firestore.FieldValue.serverTimestamp(),
            collection: {
                [guid]: generatedCollection
            },
            collectionOrder: getFirestore().FieldValue.arrayUnion(guid)
        }, { merge: true }).then(() => {
            dispatch({ type: "NEW_COLLECTION_SUCCESS" })
        }).catch(e => dispatch({ type: "NEW_COLLECTION_ERROR", e }))

    }
}

