export const saveCurrentEventEdit = () => {
    return (dispatch, getState, { getFirestore }) => {

        dispatch({ type: "START_SAVING" });
        const state = getState();

        if (state.edit.currentEventEditing) {
            const firestore = getFirestore();

            let itinRef = firestore.collection("users").doc(state.firebase.auth.uid).collection("itineraries").doc(state.edit.currentItinID);

            if (JSON.stringify(state.edit.currentEventEditing) !== JSON.stringify(state.edit.currentEventInitial)) {
                itinRef.set({
                    editedAt: firestore.FieldValue.serverTimestamp(),
                    collection: {
                        [state.edit.currentCollection]: {
                            events: {
                                [state.edit.editEventID]: state.edit.currentEventEditing
                            }
                        }
                    }
                }, { merge: true }).then(() => {
                    dispatch({ type: "CURRENT_EVENT_SAVED_SUCCESS" })
                }).catch((e) => {
                    dispatch({ type: "CURRENT_EVENT_SAVED_ERROR", e })
                })
            } else {
                dispatch({ type: "CLOSE_EVENT_EDITING" })
            }
        } else {
            dispatch({ type: "EVENT_SETUP_ERROR" })
        }
    }
}

export const makeItineraryPublic = () => {
    return (dispatch, getState, { getFirestore }) => {
        const state = getState();
        const firestore = getFirestore();
        const geoFirestore = new GeoFirestore(firestore);

        let itinRef = firestore.collection("users").doc(state.firebase.auth.uid).collection("itineraries").doc(state.edit.currentItinID);
        let publicRef = firestore.collection("publicItineraries").doc(state.edit.currentItinID)
        const geocollection = geoFirestore.collection('testPublic');
        let userRef = firestore.collection("users").doc(state.firebase.auth.uid)

        itinRef.get().then((doc) => {
            if (doc.exists) {
                let itin = doc.data();

                let locations = {}
                let coordinates = []

                itin.location.forEach(location => {
                    locations[location.name] = true
                    coordinates.push(new firestore.GeoPoint(location.lat, location.lng))
                })

                let tags = {}

                itin.tags.forEach(tag => {
                    tags[tag] = true
                })

                let images = []

                itin.collectionOrder.forEach(collectID => {
                    itin.collection[collectID].eventOrder.forEach(eventID => {
                        if (itin.collection[collectID].events[eventID].images) {
                            itin.collection[collectID].events[eventID].images.forEach(img => images.push(img.url))
                        }
                    })
                })

                let meta = {
                    name: itin.name,
                    locations: locations,
                    tags: tags,
                    views: 1,
                    coordinates: coordinates,
                    addedDate: firestore.FieldValue.serverTimestamp(),
                }

                if (images.length > 0) {
                    meta.images = images
                }

                userRef.get().then(doc => {
                    if (doc.exists) {
                        let user = doc.data()

                        let userFields = {
                            displayName: user.name,
                            userName: user.name,
                            picture: state.firebase.auth.photoURL,
                            uid: state.firebase.auth.uid
                        }

                        meta.user = userFields

                        itin.meta = meta
                        itin.id = state.edit.currentItinID

                        publicRef.set(itin).then(() => {
                            let promises = coordinates.map((coord) => {
                                geocollection.add({ coordinates: coord, ...itin }).then(() => {
                                }, (e) => {
                                    throw e
                                });
                            })

                            Promise.all(promises).then(() => {
                                dispatch({ type: "ITIN_TO_PUBLIC_SUCCESS" })
                            }).catch(e => {
                                dispatch({ type: "ITIN_TO_PUBLIC_ERROR", e })
                            })
                        }).catch((e) => {
                            dispatch({ type: "ITIN_TO_PUBLIC_ERROR", e })
                        })
                    } else {
                        dispatch({ type: "ITIN_TO_PUBLIC_ERROR", e: "User Not Valid" })
                    }
                }).catch(e => {
                    dispatch({ type: "ITIN_TO_PUBLIC_ERROR", e })
                })
            }
        }).catch(e => {
            dispatch({ type: "ITIN_TO_PUBLIC_ERROR", e })
        })
    }
}