import React, { Component } from 'react'
import { Tabs, Card } from 'antd';
import LoginForm from './LoginForm';
import SignupForm from './SignupForm'
import { right } from './Auth.module.scss'

const TabPane = Tabs.TabPane;

export default class AuthTabs extends Component {

    render() {
        return (
            <Card style={{ boxShadow: "0 12px 20px #d8e0e6" }}>
                <Tabs type="card" className={right}>
                    <TabPane tab="Login" key="1"><LoginForm /></TabPane>
                    <TabPane tab="Signup" key="2"><SignupForm /></TabPane>
                </Tabs>
            </Card>
        )
    }
}
