
export const loadItineraryToState = (id) => {
    return (dispatch, getState, { getFirestore }) => {

        const firestore = getFirestore()
        let itinRef = firestore.collection("publicItineraries").doc(id);

        itinRef.get().then((doc) => {
            if (doc.exists) {
                let loadDoc = doc.data()
                loadDoc.id = id
                dispatch({ type: "LOAD_ITINERARY_TO_VIEW", itinerary: loadDoc, currentCollectionId: loadDoc.collectionOrder[0] })
            } else {
                dispatch({ type: "VIEW_ERROR", e: "Itinerary Does Not Exist" })
            }
        }).catch((e) => {
            dispatch({ type: "VIEW_ERROR", e })
        })
    }
}

export const loadMyItineraryToState = (id) => {
    return (dispatch, getState, { getFirestore }) => {

        const { firebase } = getState();

        const firestore = getFirestore();

        if (firebase.auth.uid) {
            let itinRef = firestore.collection("users").doc(firebase.auth.uid).collection("itineraries").doc(id);

            itinRef.get().then((doc) => {
                if (doc.exists) {
                    let loadDoc = doc.data()
                    loadDoc.id = id
                    dispatch({ type: "LOAD_ITINERARY_TO_VIEW", itinerary: loadDoc, currentCollectionId: loadDoc.collectionOrder[0] })
                } else {
                    dispatch({ type: "VIEW_ERROR", e: "Itinerary Does Not Exist" })
                }
            }).catch((e) => {
                dispatch({ type: "VIEW_ERROR", e })
            })
        } else {
            dispatch({
                type: "USER_NOT_SIGNED_IN", meta: {
                    transition: (prevState, nextState, action) => ({
                        pathname: `/signup`
                    })
                }
            })
        }
    }
}

