export const clearItineraryInView = () => {
    return (dispatch, getState, { getFirestore }) => {
        dispatch({ type: "CLEAR_ITINERARY_TO_VIEW" })
    }
}

export const updateCurrentCollectionViewing = (id) => {
    return (dispatch, getState, { getFirestore }) => {
        dispatch({ type: "UPDATE_COLLECTION_VIEWING", currentCollectionId: id })
    }
}