import { Col, Icon, Row, Select, Spin } from 'antd';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import EventList from '../Edit/EventList';
import { updateCurrentCollectionViewing } from './../../store/actions/viewActions';
import ItineraryOverview from './ItineraryOverview';
import { stickyBackMenu, stickySelect } from './ItineraryViewer.module.scss';

const Option = Select.Option;


class ItineraryViewer extends Component {


    handleChange = (value) => {
        this.props.updateCurrentCollectionViewing(value);
    }

    render() {
        return (
            <div style={{ width: "100%", minWidth: "100%" }}>
                {this.props.itinerary &&
                    <div style={{ width: "100%", minWidth: "100%" }}>
                        <Row type="flex" align="middle" justify="center" className={stickyBackMenu} style={{ width: "100%", minWidth: "100%" }}>
                            <Col span={22}>
                                <a onClick={() => this.props.onBack()}><Icon type="left" /><span> Back</span></a>
                            </Col>
                        </Row>
                        <Row type="flex" align="middle" justify="center" style={{ width: "100%", minWidth: "100%" }}>
                            <Col span={22}>
                                <ItineraryOverview itinerary={this.props.itinerary}></ItineraryOverview>
                            </Col>
                        </Row>
                        <Row type="flex" align="middle" justify="center" className={stickySelect} style={{ width: "100%", minWidth: "100%" }}>
                            <Col span={22}>
                                <Select defaultValue={this.props.currentCollectionId} style={{ width: "100%" }} onChange={this.handleChange}>
                                    {
                                        this.props.itinerary.collectionOrder.map(element => {
                                            return <Option key={element} value={element}>{this.props.itinerary.collection[element].name}</Option>
                                        })
                                    }
                                </Select>
                            </Col>
                        </Row>
                        <Row type="flex" align="middle" justify="center" style={{ marginTop: "1.5em" }} style={{ width: "100%", minWidth: "100%" }}>
                            <Col span={22}>
                                <EventList dragging={this.props.dragging} viewOnly={true} events={this.props.itinerary.collection[this.props.currentCollectionId] ? this.props.itinerary.collection[this.props.currentCollectionId].events : {}} eventOrder={this.props.itinerary.collection[this.props.currentCollectionId] ? this.props.itinerary.collection[this.props.currentCollectionId].eventOrder : []}></EventList>
                            </Col>
                        </Row>
                    </div>
                }
                {!this.props.itinerary && <Spin/>}
            </div>
        )
    }
}

const mapStateToProps = (state, ownProps) => {
    return {
        ...ownProps,
        itinerary: state.view.itinerary,
        currentCollectionId: state.view.currentCollectionId,
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        updateCurrentCollectionViewing: (id) => dispatch(updateCurrentCollectionViewing(id))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(ItineraryViewer)