export const addToSavedItineraries = (id, itinerary) => {
    return (dispatch, getState, { getFirestore }) => {

        const { firebase } = getState();
        const firestore = getFirestore();

        if (firebase.auth.uid) {
            let newDocRef = firestore.collection("users").doc(firebase.auth.uid).collection("saved").doc(id);

            newDocRef.set(itinerary).then(() => {
                dispatch({ type: "ITINERARY_SAVED_SUCCESS" })
            }).catch(err => {
                dispatch({ type: "ITINERARY_SAVED_ERROR", err })
            })
        } else {
            dispatch({
                type: "USER_NOT_SIGNED_IN", meta: {
                    transition: (prevState, nextState, action) => ({
                        pathname: `/signup`
                    })
                }
            })
        }
    }
}

export const removeFromSavedItineraries = (id) => {
    return (dispatch, getState, { getFirestore }) => {
        
        const { firebase } = getState();
        const firestore = getFirestore();

        if (firebase.auth.uid) {
            let newDocRef = firestore.collection("users").doc(firebase.auth.uid).collection("saved").doc(id);

            newDocRef.delete().then(() => {
                dispatch({ type: "ITINERARY_REMOVED_SUCCESS" })
            }).catch(err => {
                dispatch({ type: "ITINERARY_REMOVED_ERROR", err })
            })
        } else {
            dispatch({
                type: "USER_NOT_SIGNED_IN", meta: {
                    transition: (prevState, nextState, action) => ({
                        pathname: `/signup`
                    })
                }
            })
        }
    }
}

export const duplicateItinerary = (itinerary) => {
    return (dispatch, getState, { getFirestore }) => {

        const { firebase } = getState();
        const firestore = getFirestore();

        if (firebase.auth.uid) {
            let newDocRef = firestore.collection("users").doc(firebase.auth.uid).collection("itineraries").doc();

            itinerary.id = newDocRef.id;
            newDocRef.set(itinerary).then(() => {
                dispatch({
                    type: "ITINERARY_DUPLICATED_SUCCESS", meta: {
                        transition: (prevState, nextState, action) => ({
                            pathname: `/edit/` + newDocRef.id
                        })
                    }
                })
            }).catch(err => {
                dispatch({ type: "ITINERARY_DUPLICATED_ERROR", err })
            })
        } else {
            dispatch({
                type: "USER_NOT_SIGNED_IN", meta: {
                    transition: (prevState, nextState, action) => ({
                        pathname: `/signup`
                    })
                }
            })
        }
    }
}