import React, { Component } from 'react'
import { Spin, Typography, Icon, Row, Col } from 'antd'
import FbImageLibrary from 'react-fb-image-grid';
const { Text, Paragraph } = Typography;

export default class PlaceDetails extends Component {

    constructor(props) {
        super(props);

        this.state = {
            place: null
        }
    }

    componentDidMount() {
        var service = new window.google.maps.places.PlacesService(document.createElement('div'));

        service.getDetails({
            placeId: this.props.placeId
        }, (place, status) => {
            if (status === window.google.maps.places.PlacesServiceStatus.OK) {
                this.setState({ place })
            }
        })
    }

    getHoursInOrder = () => {
        if (this.state.place.opening_hours && this.state.place.opening_hours.weekday_text) {
            let d = new Date();
            let day = d.getDay() - 1;
            let days = [...this.state.place.opening_hours.weekday_text];
            return days.slice(day, days.length).concat(days.slice(0, day));
        } else {
            return null
        }
    }

    render() {
        console.log(this.props)

        const photos = this.state.place && this.state.place.photos ? this.state.place.photos.map(img => img.getUrl()) : []

        return (
            <div>
                {!this.state.place && <Spin />}
                {this.state.place && console.log(this.state.place)}
                {this.state.place &&
                    <div>
                        {this.state.place.website && <div style={{ marginTop: "3px" }}><Icon type="global" style={{ marginRight: "10px" }} /><a href={this.state.place.website} target="_blank">{this.state.place.website}</a></div>}
                        {this.state.place.formatted_phone_number && <div style={{ marginTop: "3px" }}><Icon type="phone" style={{ marginRight: "10px" }} />{this.state.place.formatted_phone_number}</div>}

                        {this.state.place.opening_hours && this.state.place.opening_hours.weekday_text &&
                            <div style={{ marginTop: "3px" }}>
                                <Row type="flex" justify="start">
                                    <Col style={{ marginRight: "10px" }} >
                                        <Text strong>Hours:</Text>
                                    </Col>
                                    <Col>
                                        {
                                            this.state.place.opening_hours && this.state.place.opening_hours.open_now &&
                                            <Text style={{ color: this.state.place.opening_hours.open_now ? "green" : "red" }}>{this.state.place.opening_hours.open_now ? "Open Now" : "Closed"}</Text>
                                        }
                                        <Paragraph ellipsis={{ rows: 2, expandable: true }}>
                                            {this.getHoursInOrder().map((text) => {
                                                return (<Text>{text}<br /></Text>)
                                            })}
                                        </Paragraph>
                                    </Col>
                                </Row>
                            </div>
                        }
                        {photos.length > 0 && <div><FbImageLibrary images={photos} /></div>}
                    </div>
                }
            </div>
        )
    }
}
