import { Alert, Button, Checkbox, Form, Icon, Input } from 'antd';
import React, { Component } from 'react';
import { Icon as Icon2 } from 'react-icons-kit';
import { facebook, google } from 'react-icons-kit/fa';
import { connect } from 'react-redux';
import { sendResetEmail } from './../../store/actions/authActions';
import { btn, btn1, divider, icon } from './LoginForm.module.scss';


class LoginForm extends Component {

    constructor(props) {
        super(props);
        this.state = {
            emailSent: false,
            credentials: {
                email: null,
            }
        };
    }

    handleChange = (e) => {
        let userCreds = {
            ...this.state.credentials
        }
        userCreds[e.target.id] = e.target.value

        this.setState({
            credentials: userCreds
        });
    }

    handleSubmit = (e) => {
        e.preventDefault()

        this.props.form.validateFields((err) => {
            if (!err) {
                console.log(this.state.credentials.email)
                this.props.sendResetEmail(this.state.credentials.email);
                this.setState({ emailSent: true })
            }
        });
    }

    render() {
        const { getFieldDecorator } = this.props.form;

        return (

            <div>
                {this.state.emailSent && <div>Password Email Sent: {this.state.credentials.email}</div>}
                {!this.state.emailSent &&
                    <div>
                        <Form onSubmit={this.handleSubmit} className="login-form">
                            <Form.Item>
                                {getFieldDecorator('email', {
                                    rules: [{
                                        type: 'email', message: 'Please enter a valid email.',
                                    }, {
                                        required: true, message: 'Please enter an email.',
                                    }],
                                })(
                                    <Input id="email" onChange={this.handleChange} prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />} placeholder="Email" />
                                )}
                            </Form.Item>
                            <Form.Item>
                                <Button type="primary" htmlType="submit" className="login-form-button">
                                    Send Password Reset Email
                        </Button>
                            </Form.Item>
                        </Form>
                    </div>
                }

            </div>
        );
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        sendResetEmail: (email) => dispatch(sendResetEmail(email))
    }
}

export default connect(null, mapDispatchToProps)(Form.create()(LoginForm));