import { Button, Card, Col, Icon, Modal, Row, Tag } from 'antd';
import React, { Component } from 'react';
import FbImageLibrary from 'react-fb-image-grid';
import { connect } from 'react-redux';
import { deleteEventFromCollection } from './../../../store/actions/editActions';
import { card, gallery, hidden, itemTitle, section, tagStyle } from './EventItem.module.scss';
import PlaceDetails from './../../PlaceDetails'

const confirm = Modal.confirm

class EventItemView extends Component {

    constructor(props) {
        super(props);
        this.state = {
            details: false
        }
    }

    onDeleteClick = (id) => {
        let that = this
        confirm({
            title: "Are you sure you want to delete this event?",
            okText: 'Yes',
            cancelText: 'No',
            onOk() {
                that.props.deleteEventFromCollection(that.props.collectionID, id)
            },
            onCancel() {
                return false;
            },
        });
    }

    render() {

        const photos = this.props.item && this.props.item.images ? this.props.item.images.map(img => img.url) : []

        let tags = this.props.item && this.props.item.tags ? this.props.item.tags.map((tag, index) => {
            return <Tag key={index} className={tagStyle} color="#1890ff">{tag.toUpperCase()}</Tag>
        }) : [];
        console.log(this.props.item);

        return (
            <Card className={card} bordered={false} >
                {!this.props.viewOnly &&
                    <div className={hidden}>
                        <Button type={"danger"} shape="circle" icon="close" onClick={() => this.onDeleteClick(this.props.id)} />
                    </div>
                }
                <Row type="flex" justify="start">
                    <Col onClick={() => this.props.click()} style={{ flexGrow: 1 }} span={photos.length > 0 ? 16 : 24}>
                        {
                            this.props.item && this.props.item.name &&
                            <p className={itemTitle}>{this.props.item.name}</p>
                        }
                        {
                            (!this.props.item || !this.props.item.name) &&
                            <p className={itemTitle}>Untitled</p>
                        }
                        {
                            this.props.item && this.props.item.location &&
                            <div className={section}>
                                <Icon type="environment" style={{ marginRight: "10px" }} /><span style={{ color: "#1890ff" }}>{this.props.item.location.name}</span>
                            </div>
                        }
                        {
                            this.props.item && this.props.item.description &&
                            <div className={section}>
                                <h3>Notes:</h3>
                                <div dangerouslySetInnerHTML={{ __html: this.props.item.description }} />
                            </div>
                        }
                        {
                            tags.length > 0 && <div className={section}>{tags}</div>
                        }
                    </Col>
                    {
                        photos.length > 0 &&
                        <Col span={8} >
                            <div className={gallery}>
                                <FbImageLibrary images={photos} />
                            </div>
                        </Col>
                    }
                </Row>
                <Row>
                    {!this.state.details && this.props.item && this.props.item.location && this.props.item.location.place_id && <a onClick={() => this.setState({ details: true })}>See More</a>}
                    {this.state.details && <PlaceDetails placeId={this.props.item.location.place_id} />}
                    {this.state.details && <a onClick={() => this.setState({ details: false })}>Hide Details</a>}
                </Row>
            </Card >

        )
    }
}


const mapDispatchToProps = (dispatch) => {
    return {
        deleteEventFromCollection: (collectId, eventId) => dispatch(deleteEventFromCollection(collectId, eventId)),
    }
}

const mapStateToProps = (state) => ({
    collectionID: state.edit.currentCollection
})

export default connect(mapStateToProps, mapDispatchToProps)(EventItemView)
