import { Card, Carousel, Col, Icon, Rate, Row } from 'antd';
import React, { Component } from 'react';
import { card, carousel, img, itemTitle, noReviews } from './Search.module.scss';
import PlaceDetails from '../PlaceDetails';


export default class SearchResult extends Component {

    constructor(props) {
        super(props);
        this.state = {
            details: false
        }
    }



    render() {
        let cover = <Carousel className={carousel} afterChange={this.onChange}>
            {
                this.props.result && this.props.result.photos && this.props.result.photos.map((photo) => {
                    return (
                        <div><img className={img} alt="example" src={photo.getUrl()} /></div>
                    )
                })
            }
        </Carousel>

        return (
            <Card className={card} bordered={false}
                cover={this.props.result.photos && this.props.result.photos.length > 0 ? cover : null}
            >
                <Row type="flex" justify="start">
                    <Col style={{ flexGrow: 1 }} >
                        <div>
                            <Rate allowHalf disabled defaultValue={this.props.result.rating ? parseFloat((Math.round(this.props.result.rating * 2) / 2).toFixed(1)) : 0} style={{ fontSize: 15, }} />
                            {
                                (!this.props.result.rating) && <span className={noReviews}>(No Reviews)</span>
                            }
                            {
                                (this.props.result.rating && this.props.result.user_ratings_total) && <span className={noReviews}>({this.props.result.user_ratings_total})</span>
                            } 
                        </div>

                        <p className={itemTitle}>
                            {this.props.result.name}
                        </p>

                        {
                            this.props.result.formatted_address &&
                            <div>
                                <Icon type="environment" style={{ marginRight: "10px" }} /><span style={{ color: "#1890ff" }}>{this.props.result.formatted_address}</span>
                            </div>
                        }

                        {
                            this.props.result.html_attributions.length > 0 && <div>{this.props.result.html_attributions.length}</div>
                        }
                    </Col>
                </Row>
                <Row style={{marginTop: "10px"}}>
                    {!this.state.details && <a onClick={() => this.setState({ details: true })}>See More</a>}
                    {this.state.details && <PlaceDetails placeId={this.props.result.place_id} />}
                </Row>
            </Card >
        )
    }
}