import { Card, Carousel, Modal, Icon, Menu, Row, Tag } from 'antd';
import React, { Component } from 'react';
import { withRouter } from "react-router";
import { deleteItinerary } from './../../../store/actions/editActions'
import { connect } from "react-redux";
import { cardContainer, carousel, img, item, tagStyle } from './../MyItineraryViewer/ItinCard.module.scss';
import { removeFromSavedItineraries, duplicateItinerary } from './../../../store/actions/saveActions';
import { Marker, StaticGoogleMap } from 'react-static-google-map';
import { loadMySavedItineraryToState } from './../../../store/actions/viewActions'
const { Meta } = Card;
const confirm = Modal.confirm

class ItinCardSaved extends Component {

    onDeleteClick = () => {
        let that = this;
        confirm({
            title: "Are you sure you want to delete this itinerary?",
            okText: 'Yes',
            cancelText: 'No',
            onOk() {
                that.props.removeFromSavedItineraries(that.props.itin.id)
            },
            onCancel() {
                return false;
            },
        });
    }

    onDuplicateClick = () => {
        let that = this;
        confirm({
            title: "Duplicate this itinerary & save it to your itineraries?",
            okText: 'Yes',
            cancelText: 'No',
            onOk() {
                that.props.duplicateItinerary(that.props.itin)
            },
            onCancel() {
                return false;
            },
        });
    }

    render() {

        let tags = this.props.itin.tags ? this.props.itin.tags.map((tag) => {
            return <Tag className={tagStyle} color="#1890ff">{tag}</Tag>
        }) : [];

        let locationString = this.props.itin && this.props.itin.location && this.props.itin.location.map((location, index) => {
            return <span key={index}>{index + 1 !== this.props.itin.location.length ? location.name + " | " : location.name}</span>
        })

        let images = []

        this.props.itin.collectionOrder.forEach(collectID => {
            this.props.itin.collection[collectID].eventOrder.forEach(eventID => {
                if (this.props.itin.collection[collectID].events[eventID].images) {
                    this.props.itin.collection[collectID].events[eventID].images.forEach(img => images.push(img.url))
                }
            })
        })

        let cover = <Carousel dots={false} autoplay className={carousel} effect={"fade"} afterChange={this.onChange}>
            {
                images.map((photo) => {
                    return (
                        <img className={img} alt="example" src={photo} />
                    )
                })
            }
            <StaticGoogleMap
                onClick={() => this.props.loadMySavedItineraryToState(this.props.itin.id)}
                className={img}
                center={this.props.itin.location[0].name}
                zoom="13"
                size="350x200"
                apiKey="AIzaSyAjMva2YUsi-3w0Nf_hXzDuuH4TDM1qSHA"
            >
                <Marker location={this.props.itin.location[0].name} />
            </StaticGoogleMap>
        </Carousel>

        return (
            <div className={item}>
                <Card
                    style={{ marginTop: "1em" }}
                    hoverable
                    cover={images.length > 0 ? cover : null}
                    actions={[<Icon onClick={() => { this.onDuplicateClick() }} type="copy" />, <Icon onClick={() => this.onDeleteClick()} type="delete" />]}
                >
                    <div onClick={() => this.props.loadMySavedItineraryToState(this.props.itin.id)}>
                        <Row>
                            <Meta
                                title={this.props.itin.name}
                                description={locationString}
                            />
                        </Row>
                        <Row className={cardContainer}>
                            {tags.slice(0, 6)}
                        </Row>
                    </div>
                </Card>
            </div >
        )
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        loadMySavedItineraryToState: (id) => dispatch(loadMySavedItineraryToState(id)),
        duplicateItinerary: (itinerary) => dispatch(duplicateItinerary(itinerary)),
        removeFromSavedItineraries: (id) => dispatch(removeFromSavedItineraries(id))
    }
}

export default withRouter(connect(null, mapDispatchToProps)(ItinCardSaved));