import React, { Component } from 'react'
import PageLayout from '../Core/PageLayout'
import MyItineraryViewer from './MyItineraryViewer';
import { Row, Col, Button, Icon, Avatar, Radio, Drawer } from 'antd'
import { createNewItinerary } from './../../store/actions/editActions'
import { connect } from 'react-redux';
import { clearItineraryInView } from '../../store/actions/viewActions';
import MySavedItineraryViewer from './MySavedItineraryViewer';
import ItineraryViewer from './../ItineraryViewer'
import { DragDropContext } from 'react-beautiful-dnd';

const RadioButton = Radio.Button;
const RadioGroup = Radio.Group;

class Profile extends Component {

    constructor(props) {
        super(props)
        this.state = {
            view: "1"
        }
    }

    onChange = (e) => {
        this.setState({ view: e.target.value })
    }
    onDragEnd = () => { }

    render() {
        console.log(this.props.auth)
        return (
            <PageLayout>
                <Row type="flex" justify="center" style={{ marginTop: "50px" }}>
                    {this.props.auth.photoURL && <Avatar size={240} src={this.props.auth.photoURL} />}
                    {!this.props.auth.photoURL && <Avatar size={240} style={{ marginRight: '0px' }} icon="user" />}
                </Row>
                <Row type="flex" justify="center" >
                    <h1 style={{ fontSize: 50 }}>{this.props.auth.displayName ? "Hey " + this.props.auth.displayName + "," : "Where to next?"}</h1>
                </Row>
                <Row type="flex" justify="center" style={{ marginTop: "50px" }}>
                    <RadioGroup size="large" buttonStyle="solid" style={{ zIndex: 998 }} onChange={this.onChange} defaultValue="1">
                        <RadioButton value="1">My Itineraries</RadioButton>
                        <RadioButton value="2">Saved</RadioButton>
                    </RadioGroup>
                </Row>
                {this.state.view === "1" &&

                    <Row type="flex" justify="center">
                        <Col xs={22} sm={22} md={22} lg={20} xl={20} xxl={20} style={{ paddingTop: "100px", paddingBottom: "100px" }}>
                            <h1 style={{ marginBottom: "20px" }}>My Itineraries</h1>
                            <Button style={{ marginBottom: "20px" }} onClick={() => this.props.createNewItinerary()} type="primary"><Icon type="plus" />Create New Itinerary</Button>
                            <MyItineraryViewer />
                        </Col>
                    </Row>
                }
                {this.state.view === "2" &&

                    <Row type="flex" justify="center">
                        <Col xs={22} sm={22} md={22} lg={20} xl={20} xxl={20} style={{ paddingTop: "100px", paddingBottom: "100px" }}>
                            <h1 style={{ marginBottom: "20px" }}>My Saved Itineraries</h1>
                            <MySavedItineraryViewer />
                        </Col>
                    </Row>
                }
                <Drawer
                    placement="right"
                    closable={false}
                    onClose={this.onClose}
                    visible={this.props.itineraryInFocus}
                    width={window.innerWidth <= 760 ? "100vw" : "50vw"}
                >
                    <DragDropContext onDragEnd={this.onDragEnd} >
                        <ItineraryViewer dragging={this.props.dragging} match={this.props.match} onBack={() => this.props.clearItineraryInView()}></ItineraryViewer>
                    </DragDropContext>
                </Drawer>
            </PageLayout>
        )
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        createNewItinerary: () => dispatch(createNewItinerary()),
        clearItineraryInView: () => dispatch(clearItineraryInView())
    }
}

const mapStateToProps = (state, ownProps) => ({
    auth: state.firebase.auth,
    itineraryInFocus: state.view.itinerary,
})


export default connect(mapStateToProps, mapDispatchToProps)(Profile); 