import { GeoFirestore } from 'geofirestore';


export const closeDiscoverModal = () => {
    return (dispatch, getState, { getFirestore }) => {
        dispatch({ type: "CLOSE_DISCOVER_MODAL" })
    }
}

/* 
    {
        lat: 
        lng: 
    }
*/
export const getPublicQueryResults = (query) => {

    return (dispatch, getState, { getFirestore }) => {
        const firestore = getFirestore()
        const publicItineraries = firestore.collection('publicItineraries');

        let queryRef = null
        let queryName = ""

        if (query.type === 'location') {
            let location = query.location
            // location.radius = 5
            // geoQuery = geocollection.near({ center: new firestore.GeoPoint(location.lat, location.lng), radius: location.radius });
            queryRef = publicItineraries.where('meta.locations.' + location.address, '==', true)
            queryName = location.address
        } else {
            queryRef = publicItineraries.where('meta.user.displayName', '>=', query.user);
            queryName = query.user
        }

        dispatch({ type: "QUERY_LOADING" })
        queryRef.limit(15).get().then((querySnapshot) => {
            let formatted = [];
            querySnapshot.docs.forEach((doc) => {
                formatted.push({ ...doc.data(), id: doc.id })
            })
            let nextQuery = querySnapshot.docs.length >= 15 ? queryRef.limit(15).startAfter(querySnapshot) : null;
            dispatch({ type: "QUERY_SUCCESS", lastSnapshot: querySnapshot, nextQuery: nextQuery, formattedResults: formatted, currentQuery: queryName })
        }).catch(err => {
            dispatch({ type: "QUERY_ERROR", err })
        })
    }
}

export const querySpecificPublicDocument = (docId) => {

    return (dispatch, getState, { getFirestore }) => {

        const firestore = getFirestore()
        let ref = firestore.collection("publicItineraries")
        ref.doc(docId)

        dispatch({ type: "SPECIFIC_QUERY_LOADING" })

        ref.get().then((doc) => {
            if (doc.exists) {
                dispatch({ type: "SPECIFIC_QUERY_SUCCESS", focused: doc.data() })
            } else {
                throw ("Itinerary Not Found")
            }
        }).catch(err => {
            dispatch({ type: "SPECIFIC_QUERY_ERROR", err })
        })
    }
}



