export const addToPublicIndex = (newItin) => {
    return (dispatch, getState, { getFirestore }) => {
        const firestore = getFirestore();
        const geofirestore = new GeoFirestore(firestore);
        const geocollection = geofirestore.collection('publicIndex');

        geocollection.add({ coordinates: new firestore.GeoPoint(newItin.lat, newItin.lng), ...newItin }).then(() => {
            dispatch({ type: "PUBLIC_ADD_SUCCESS" })
        }).catch((err) => {
            dispatch({ type: "PUBLIC_ADD_ERROR" }, err)
        })
    }
}

export const loadMorePublicQueryResults = () => {
    return (dispatch, getState, { getFirestore }) => {
        const firestore = getFirestore()
        const state = getState();

        let queryRef = state.query.nextQuery;

        let query = getState().query.currentQuery
        query.radius = query.radius + 5;
        // const geoQuery = geocollection.near({ center: new firestore.GeoPoint(query.lat, query.lng), radius: query.radius });

        dispatch({ type: "QUERY_LOADING" })

        queryRef.get().then((querySnapshot) => {
            let formatted = state.query.formattedResults;
            querySnapshot.docs.forEach((doc) => {
                formatted.push({ ...doc.data(), id: doc.id })
            })
            dispatch({ type: "QUERY_SUCCESS", formattedResults: formatted, lastSnapshot: querySnapshot, currentQuery: state.query.currentQuery })
        }).catch(err => {
            dispatch({ type: "QUERY_ERROR", err })
        })
    }
}

