import React, { Component } from 'react';
import EventItemEdit from './EventItemEdit';
import EventItemView from './EventItemView';

export default class EventItem extends Component {
    render() {
        return (
            <div>
                {
                    !this.props.editing &&
                    <EventItemView click={() => this.props.click()}viewOnly={this.props.viewOnly} item={this.props.item} id={this.props.id}/>
                }
                {
                    this.props.editing &&
                    <EventItemEdit item={this.props.item} id={this.props.id}/>
                }
            </div>
        )
    }
}
