import React, { Component } from 'react'
import { Tabs, Card, Spin, Row, Col } from 'antd'
import { image, module, overlay } from './LocationCarousel.module.scss'
import Slider from "react-slick";
import { getPublicQueryResults } from './../../store/actions/queryActions'
import { withRouter } from "react-router";
import { firestoreConnect } from 'react-redux-firebase';
import { connect } from 'react-redux';
import { compose } from 'redux';

class LocationCarousel extends Component {
  render() {
    var settings = {
      dots: true,
      infinite: true,
      speed: 500,
      slidesToShow: 4,
      slidesToScroll: 4,
      initialSlide: 0,
      responsive: [
        {
          breakpoint: 1024,
          settings: {
            slidesToShow: 3,
            slidesToScroll: 3,
            infinite: true,
            dots: true
          }
        },
        {
          breakpoint: 600,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 2,
            initialSlide: 2
          }
        },
        {
          breakpoint: 480,
          settings: {
            slidesToShow: 1,
            slidesToScroll: 1
          }
        }
      ],
      nextArrow: null,
      prevArrow: null,
      autoplay: true,
      autoplaySpeed: 5000,
      pauseOnHover: true
    };

    return (
      <div className="location">
        {
          !this.props.trendingLocations &&
          <Row style={{ width: "100%" }} type="flex" justify="center">
            {
              <Col><Spin /></Col>
            }
          </Row>
        }
        {
          this.props.trendingLocations &&
          <Slider {...settings}>
            {Object.keys(this.props.trendingLocations).map((location) => {
              return (
                <Card
                  hoverable
                  onClick={() => {
                    this.props.getPublicQueryResults({ type: 'location', location: { address: this.props.trendingLocations[location].address } })
                    this.props.history.push('/discover');
                  }}
                  bodyStyle={{ padding: "0" }}
                >
                  <div className={module} style={{ backgroundImage: 'url(' + this.props.trendingLocations[location].image + ')' }}>
                    <div className={overlay} />
                    <header>
                      <h1>
                        {this.props.trendingLocations[location].name}
                      </h1>
                    </header>
                  </div>
                </Card>
              )
            })}
          </Slider>
        }
      </div>
    );
  }

}


const mapStateToProps = (state, ownProps) => {
  return {
    ...ownProps,
    trendingLocations: state.firestore.data['locations'],
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    getPublicQueryResults: (query) => dispatch(getPublicQueryResults(query))
  }
}

export default compose(connect(mapStateToProps, mapDispatchToProps), withRouter, firestoreConnect(props => [{
  collection: 'locations',
}]))(LocationCarousel);


