import { Icon, Rate, Row, Typography } from 'antd';
import React, { Component } from 'react';
import { noReviews, section } from './ItineraryViewer.module.scss';

const { Paragraph } = Typography;

export default class ItineraryOverview extends Component {
  render() {
    let locationString = this.props.itinerary.location.map((location, index) => {
      return <span key={index}>{index + 1 !== this.props.itinerary.location.length ? location.name + " | " : location.name}</span>
    })

    return (
      <div style={{ width: "100%" }}>
        <h1>{this.props.itinerary.name ? this.props.itinerary.name : "Untitled"}</h1>
        <Row className={section}>
          <Rate allowHalf disabled defaultValue={this.props.itinerary && this.props.itinerary.rating ? parseFloat((Math.round(this.props.itin.rating * 2) / 2).toFixed(1)) : 0} style={{ fontSize: 15, }} />
          {
            (!this.props.itinerary || !this.props.itinerary.rating) && <span className={noReviews}>(No Reviews)</span>
          }
          {
            (this.props.itinerary && this.props.itinerary.rating && this.props.itinerary.ratingCount) && <span className={noReviews}>({this.props.itin.ratingCount})</span>
          }
        </Row>
        <Icon type="environment" style={{ marginRight: "10px" }} /><span style={{ color: "#1890ff" }}>{locationString}</span>
        {this.props.itinerary.description &&
          <Row className={section}>
            <Paragraph ellipsis={{ rows: 3, expandable: true }}>
              <span dangerouslySetInnerHTML={{ __html: this.props.itinerary.description }}></span>
            </Paragraph>
          </Row>
        }
      </div>
    )
  }
}
